<template>
    <div v-resize="resize">
        <canvas class="canvas-size" ref="gcodeViewer"></canvas>
        <div class="main-container">
            <v-row no-gutters>
                <v-col cols="12">
                    <v-btn title="Load File" @click="chooseFile"><v-icon>mdi-upload</v-icon> Upload</v-btn>
                    <v-btn title="Reload With New Settings" @click="reload" class="ml-1"><v-icon>mdi-reload</v-icon> Reload</v-btn>
                </v-col>
				<v-col cols="12">
					<span class="small-font-note">Reload after changing settings.</span>
				</v-col>

                <v-col cols="12">
                    <v-text-field hint="Voxel Width" persistent-hint v-model="voxelWidth" class="input-width"></v-text-field>
                </v-col>
                <v-col cols="12">
                    <v-text-field hint="Voxel Height" persistent-hint v-model="voxelHeight" class="input-width"></v-text-field>
                </v-col>
                <v-col cols="12">
                    <v-checkbox hint="Feed Rate Coloring" persistent-hint v-model="feedColoring" class="input-width"></v-checkbox>
                </v-col>
                <v-col cols="6"> </v-col>
                <v-col cols="6">
                    <v-btn title="Reset Camera" @click="resetCam">
                        <v-icon>mdi-camera</v-icon>
                    </v-btn>
                </v-col>
            </v-row>
            <input ref="fileInput" type="file" :accept="'.g,.gcode,.gc,.gco,.nc,.ngc,.tap'" hidden @change="fileSelected" multiple />
        </div>
        <div class="left-container">
			<v-row no-gutters class="input-width">
				<v-col cols="12" v-show="feedColoring">
					<v-text-field hint="Min Feed Rate (mm/m)" persistent-hint v-model="minColorRate" class="input-width"></v-text-field>
				</v-col>
				<v-col cols="12" v-show="feedColoring">
					<v-text-field hint="Max Feed Rate (mm/m)" persistent-hint v-model="maxColorRate" class="input-width"></v-text-field>
				</v-col>
				<v-col cols="12" v-show="!feedColoring">
					<v-card v-for="(extruder, index) in extruderColors" :key="index" >
						<v-card-title> Tool {{index}}</v-card-title>
						<v-card-text>
							<color-picker :editcolor="extruder" @updatecolor="value => {updateExtruderColor(index, value);}"></color-picker>
						</v-card-text>
					</v-card>
				</v-col>
				<v-col cols="12" v-show="!feedColoring" >
					<v-btn @click="resetExtruderColors" block>Reset Colors</v-btn>
				</v-col>
			</v-row>
        </div>
        <div class="loading-progress">
            <v-progress-linear rounded v-show="loading" height="15" :value="loadingProgress" class="disable-transition">
                {{loadingProgress}}%
            </v-progress-linear>
        </div>
    </div>
</template>


<style scoped>

.small-font-note {
	font-size : xx-small;
}

.canvas-size {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	right: 0;
	bottom: 0;
	background-color: yellow;
}

.main-container {
	position: absolute;
	right: 5px;
	top: 5px;
	width: 250px;
}

.loading-progress {
	position: absolute;
	width: 50%;
	left: 0px;
	margin-left: 25%;
	top: 5px;
	z-index: 19 !important;
}

.input-width {
	width: 250px;
}

/* Transitions lag when trying to show loading progress */
.disable-transition {
	transition: none !important;
}

.left-container {
	position: absolute;
	top: 5px;
	left: 5px;
	width: 10vw;
}
</style>

<script>
'use strict';

import GCodeViewer from './viewer/gcodeviewer.js';
import { RenderMode, ColorMode } from './viewer/gcodeprocessor.js';

let viewer = null;

export default {
	data() {
		return {
			loading: false,
			loadingProgress: 0,
			voxelWidth: 3,
			voxelHeight: 3,
			feedColoring: true,
			minColorRate: 0,
			maxColorRate: 6000,
			extruderColors: ['#00FFFF', '#FF00FF', '#FFFF00', '#000000', '#FFFFFF'],
		};
	},
	computed: {},
	mounted() {
		viewer = new GCodeViewer(this.$refs.gcodeViewer);
		viewer.init();

		viewer.gcodeProcessor.loadingProgressCallback = progress => {
			this.loadingProgress = Math.ceil(progress * 100);
			if (this.loadingProgress > 99) {
				this.loading = false;
			}
		};
		this.extruderColors = viewer.getExtruderColors();
		viewer.setCursorVisiblity(false);
		viewer.gcodeProcessor.colorMode = ColorMode.Feed;
		viewer.gcodeProcessor.renderMode = RenderMode.voxel;
		viewer.gcodeProcessor.meshBreakPoint = Number.MAX_VALUE; //We are going to overwrite the breakpoint for voxels (for now)  Voxel Engine will run it's own SPS. SPS per layer height?
		viewer.setZClipPlane(1000000,-1000000); 
	},
	methods: {
		resize() {
			if (viewer !== null) {
				viewer.resize();
			}
		},
		async fileSelected(e) {
			this.updateViewerSettings();

			const reader = new FileReader();
			reader.addEventListener('load', async event => {
				const blob = event.target.result;
				await viewer.processFile(blob);
				//this.setGCodeValues();
				this.loading = false;
			});
			this.loading = true;
			viewer.gcodeProcessor.colorMode = this.feedColoring ? ColorMode.Feed : ColorMode.Color;
			reader.readAsText(e.target.files[0]);
			e.target.value = '';
		},
		updateExtruderColor(index, value) {
			this.$set(this.extruderColors, index, value);
			viewer.saveExtruderColors(this.extruderColors);
		},
		chooseFile() {
			this.$refs.fileInput.click();
		},
		resetCam() {
			viewer.resetCamera();
		},
		reload() {
			this.loading = true;
			this.updateViewerSettings();
			viewer.reload();
		},
		updateViewerSettings() {
			viewer.saveExtruderColors(this.extruderColors);
			viewer.gcodeProcessor.voxelWidth = isNaN(this.voxelWidth) || this.voxelWidth === '' ? 3 : this.voxelWidth;
			viewer.gcodeProcessor.voxelHeight = isNaN(this.voxelHeight) || this.voxelHeight === '' ? 3 : this.voxelHeight;
			viewer.gcodeProcessor.minColorRate = isNaN(this.minColorRate) ? 0 : this.minColorRate;
			viewer.gcodeProcessor.maxColorRate = isNaN(this.maxColorRate) ? 6000 : this.maxColorRate;
			viewer.gcodeProcessor.colorMode = this.feedColoring ? ColorMode.Feed : ColorMode.Color;
		},
		resetExtruderColors() {
			this.extruderColors = ['#00FFFF', '#FF00FF', '#FFFF00', '#000000', '#FFFFFF'];
			viewer.saveExtruderColors(this.extruderColors);
			this.extruderColors.forEach((v, i) => {
				this.$set(this.extruderColors, i, v);
			});
			this.extruderColors = viewer.getExtruderColors();
		},
		async loadBenchy() {
			let benchy = await fetch('./benchy_color.gcode');
			let blob = await benchy.blob();
			let text = await blob.text();
			if (blob === undefined) return;
			this.updateViewerSettings();
			await viewer.processFile(text);
		},
	},
};
</script>



